.DatePicker {
  display: -ms-flexbox;
  display: flex;
  width: 100%; }
  .DatePicker > * {
    height: 44px;
    background: #f9f9f9;
    border: 1px solid #ccc; }
    .DatePicker > *:focus {
      border: 1px solid #2065ce;
      outline: 0; }
  .DatePicker__Day {
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px; }
  .DatePicker__Year {
    width: 64px;
    padding: 4px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px; }



